// components/ProgramCarousel.jsx
'use client';

import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';

import { Pagination, Autoplay } from 'swiper/modules';
import Image from 'next/image';

import techlearnImg from '../pages/programs/images/abouttechlearn.webp';
import finlearnImg from '../public/images/finlearn.webp';
import clinomicImg from '../public/images/clinomic.webp';

const ProgramCarousel = () => {
  const programs = [
    {
      title: 'TechLearn',
      desc: 'Career-oriented training in Full-Stack, DevOps & Cloud.',
      image: techlearnImg,
    },
    {
      title: 'FinLearn',
      desc: 'Industry-ready programs in Banking, Finance & Accounting.',
      image: finlearnImg,
    },
    {
      title: 'Clinomic',
      desc: 'Clinical Research training aligned with global standards.',
      image: clinomicImg,
    },
  ];

  return (
    <section className="carouselContainer">
      <Swiper
        modules={[Pagination, Autoplay]}
        spaceBetween={30}
        slidesPerView={1}
        pagination={{ clickable: true }}
        autoplay={{ delay: 3000 }}
        loop
      >
        {programs.map((program, index) => (
          <SwiperSlide key={index}>
            <div className="slideCard">
              <Image src={program.image} alt={program.title} width={600} height={400} className="slideImage" />
              <h3>{program.title}</h3>
              <p>{program.desc}</p>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>

      <style jsx>{`
        .carouselContainer {
          max-width: 900px;
          margin: 4rem auto;
          padding: 1rem;
        }
        .slideCard {
          text-align: center;
          padding: 1rem;
          background: white;
          border-radius: 1rem;
          box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
        }
        .slideImage {
          border-radius: 1rem;
          margin-bottom: 1rem;
        }
        h3 {
          margin: 0.5rem 0;
          font-size: 1.5rem;
          color: #1e1b4b;
        }
        p {
          color: #374151;
        }
      `}</style>
    </section>
  );
};

export default ProgramCarousel;
